@include font('RobotoCondensed-Bold', 'RobotoCondensed-Bold');
@include font('robotocondensed-regular', 'robotocondensed-regular');
@include font('roboto-bold', 'roboto-bold');
@include font('roboto-light', 'roboto-light');
@include font('roboto-medium', 'roboto-medium');
@include font('roboto-regular', 'roboto-regular');
@include font('roboto-thin', 'roboto-thin');

@mixin rob {
	font-family: 'roboto-regular', sans-serif; }
@mixin robc {
	font-family: 'robotocondensed-regular', sans-serif; }
@mixin robc-b {
	font-family: 'RobotoCondensed-Bold', sans-serif; }
@mixin rob-b {
	font-family: 'roboto-bold', sans-serif; }
@mixin rob-l {
	font-family: 'roboto-light', sans-serif; }
@mixin rob-m {
	font-family: 'roboto-medium', sans-serif; }
@mixin rob-t {
	font-family: 'roboto-thin', sans-serif; }
