.header {
	position: fixed;
	z-index: 50;
	left: 0;
	width: 100%;
	top: 19px;
	overflow: hidden;
	&__inner {
		display: flex; }
	&__menu, &__phone {
		position: relative;
		&:before {
			content: '';
			position: absolute;
			height: 100%;
			width: 3000px;
			z-index: -1;
			top: 0; } }
	&__menu {
		font-size: 0;
		padding-left: 171px;
		flex-grow: 1;
		margin-right: 15px;
		&:before {
			background: $blue;
			right: 0; } }
	&__menu-item {
		display: inline-block;
		vertical-align: top;
		margin-right: 55px;
		&:last-of-type {
			margin-right: 0; }
		&.is-active, &:hover {
			a {
				&:after {
					opacity: 1; } } }
		a {
			color: $white;
			font-size: 24px;
			@include robc;
			display: block;
			padding: 17px 0 16px;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				height: 4px;
				background-color: $green;
				left: -7px;
				right: -10px;
				bottom: 1px;
				opacity: 0;
				@include trans; } } }
	&__phone {
		flex-grow: 0;
		flex-shrink: 0;
		padding: 12px 16px 13px 65px;
		color: $white;
		font-size: 30px;
		@include robc-b;
		background: $green;
		display: block;
		&:before {
			background: $blue;
			transform: translateX(100%);
			right: -15px; }
		i {
			display: block;
			font-size: 0;
			line-height: 1;
			position: absolute;
			left: 15px;
			top: 50%;
			transform: translateY(-50%); }
		svg {
			fill: $white;
			width: 35px;
			height: 35px; } } }
